/**
 * @description 突变(处理状态改变)
 */
import * as types from './mutation-types'

export default {
  // 获取用户数据
  [types.SET_USER] (state, user) {
    if (user) {
      state.user = user
    } else {
      state.user = {}
    }
  },
  // 获取页面数据
  [types.SET_PAGE_STYLE] (state, pageStyle) {
    if (pageStyle) {
      state.pageStyle = pageStyle
    } else {
      state.pageStyle = {}
    }
  }
}



