/**
 * @description 管理事件(行为)
 */
// 引入 状态(类型)，用于提交到 mutations
import * as types from './mutation-types'

export default {
  // 用户数据
  setUser ({commit}, user) {
    commit(types.SET_USER, user)
  },
  // 页面样式数据
  setPageStyle ({commit}, pageStyle) {
    commit(types.SET_PAGE_STYLE, pageStyle)
  }
}



