import View from '@/views/view'

const routes = [
  {
    path: '/',
    title: '主页',
    name: 'ViewPage',
    icon: 'md-analytics',
    component: View,
    children: [
      {
        path: '/test',
        name: 'test',
        title: 'three测试',
        icon: 'md-analytics',
        component: resolve => require(['@/views/test/index'], resolve),
        children: [
          {
            path: '/test/three1-demo1',
            name: 'three1Demo1',
            title: '显示立方体',
            icon: 'md-analytics',
            component: () => import('@/views/test/three1/demo1.vue')
          }, {
            path: '/test/three1-ani-demo1',
            name: 'aniDemo1',
            title: '旋转立方体',
            icon: 'md-analytics',
            component: () => import('@/views/test/three1/ani-demo1.vue')
          }, {
            path: '/test/three1-edit-demo1',
            name: 'editDemo1',
            title: '操作立方体',
            icon: 'md-analytics',
            component: () => import('@/views/test/three1/edit-demo1.vue')
          }, {
            path: '/test/three1-add-multi-cube',
            name: 'addMultiCube',
            title: '多个立方体',
            icon: 'md-analytics',
            component: () => import('@/views/test/three1/add-multi-cube.vue')
          }, {
            path: '/test/babylon-demo',
            name: 'babylonDemo',
            title: '显示动画',
            icon: 'md-analytics',
            component: () => import('@/views/test/babylon/demo.vue')
          }
        ]
      }
    ]
  }
]

export default routes



